<template>
    <div class="search_warper" :style="{'width':width+'rem'}">

        <input type="search" class="search_input" v-on:focus="focusCallBack"  v-on:keyup.enter="searchSubmit($event.target.value)" v-focus v-bind:placeholder="placeholder" @click="searchClick" v-if="autofocus" />
        <input type="search" class="search_input" v-on:focus="focusCallBack" v-on:keyup.enter="searchSubmit($event.target.value)" v-bind:placeholder="placeholder" @click="searchClick" v-else />
    </div>
</template>

<script>
export default {
    props: {
        placeholder: { type: String, default: () => '搜索考场/候选人' },
        searchClick: { type: Function, default: () => { } },
        searchSubmit: { type: Function, default: () => { } },
        focusCallBack: { type: Function, default: () => {console.log(1) } },
        autofocus: { type: Boolean, default: () => { return false; } },
        width: { type: Number, default: 12 },

    }

}
</script>

<style lang="less" scoped>
@import (reference) "../../assets/css/base.less";
.search_warper {
  display: inline-block;
  vertical-align: middle;
  background: @bgcolor;
  width: 480/@r;
  height: 60/@r;
  border-radius: 30/@r;
  box-sizing: border-box;
  padding: 0 0 0 20/@r;
  &:before {
    display: inline-block;
    width: 32/@r;
    height: 32/@r;
    background: url("../../assets/images/icon.png") -9/@r -9/@r;
    background-size: 640/@r 200/@r;
    vertical-align: middle;
    content: "";
  }
  .search_input {
    font-size: 26/@r;
    margin-left: 16/@r;
    width: 400/@r;
  }
}
</style>




