<template>
    <div>
        <common-header title="考场搜索" />
        <div class="search_top">
            <search placeholder="搜索考场/候选人" :focusCallBack="focusCallBack" :autofocus="true" :width="12.5" :searchSubmit="searchSubmit" />
            <span class="search_cancel" @click="searchCancel">取消</span>
        </div>
        <div class="tab_warp" @click="changeTab($event)">
            <span class="tab_item  left" v-bind:class="{active:searchType===1}" data-type='1'>考场</span>
            <span class="tab_item right" v-bind:class="{active:searchType===2}" data-type='2'>候选人</span>
        </div>
        <div class="search_recent" :class="{no_data:searchRecord.length<=0}" v-show='curState==1'>
            <p class="search_recent_tite">最近搜索</p>
            <ul class="search_recent_list">
                <li class="search_recent_item" v-for="(item,index) in searchRecord" v-bind:key="index" @click="searchRecentClick">
                    <span class="content">{{item}}</span>
                    <i class="icon_del" @click="delSearchRecord(searchRecord.length-index)"></i>
                </li>
            </ul>
            <div class="no_data_warp">
                <div class="no_data_img">
                </div>
                <p>暂无搜索记录</p>
            </div>
        </div>
        <ul v-show='curState==2'>
            <examitem v-for="(item,index) in examSearchRoomList" v-bind:key="item.RoomID" :examinfo='item' />
        </ul>

    </div>
</template>

<script>
import search from './common/SearchInput';
import examitem from './common/ExaminationItem';
import Header from './common/Header';
import { mapGetters, mapActions } from "vuex";
import { localStorageKey } from '../../static/dicConfig';
export default {
    data() {
        return {
            searchRecord: [],
            //考场默认搜索参数
            paramExam: { roomName: null, cityId: null, pageIndex: 1, pageSize: 5 },
            //候选人默认搜索参数
            paramPerson: { roomName: null, cityId: null, pageIndex: 1, pageSize: 5 },
            //搜索目标 默认为考场 1：考场 2：候选人
            searchType: 1,
            //当前状态,是否处于搜索结果显示状态 1 显示最近搜索 2 显示搜索结果
            curState: 1,
        }
    },
    methods: {
        ...mapActions([
            "getExamSearchRoomList",
        ]),
        searchCancel() {
            this.$router.push({ name: 'ExaminationRoom' });
        },
        /**
         * @method 点击搜索
         * @param {String} 搜索内容
         */
        searchSubmit(txt) {
            //todo 移除搜索聚焦
            this.curState = 2;
            this.saveSearchRecord(txt);
           if (this.searchType === 1) {
                this.showExamRoomList(txt);
            } else if (this.searchType === 2) {
                //todo 搜索候选人列表
                this.showExamRoomList(txt);
            }
        },
        /**
         * @method 点击最近搜索记录进行检索
         */
        searchRecentClick(txt) {
            if (this.searchType === 1) {
                this.showExamRoomList(txt);
            } else if (this.searchType === 2) {
                //todo 搜索候选人列表
                this.showExamRoomList(txt);
            }
        },
        /**
         * 搜索框聚焦,切换到最近搜索记录
         */
        focusCallBack() {
            this.curState = 1;
        },
        /**
         * @method 保存搜索记录
         */
        saveSearchRecord(txt) {

            let old = JSON.parse(localStorage.getItem(`${localStorageKey.search_exam_key}${this.searchType}`));
            if (old && Array.isArray(old)) {
                if (old.length >= 5) {
                    //弹出第一个 先进先出
                    old.shift();
                }
            }
            else {
                old = [];
            }
            old.push(txt);
            localStorage.setItem(`${localStorageKey.search_exam_key}${this.searchType}`, JSON.stringify(old));
            this.getSearchRecord();
        },
        /**
         * @method 删除搜索记录
         */
        delSearchRecord(index) {
            let old = JSON.parse(localStorage.getItem(`${localStorageKey.search_exam_key}${this.searchType}`));
            if (old && Array.isArray(old)) {
                old.splice(index - 1, 1);
            }
            localStorage.setItem(localStorageKey.search_exam_key, JSON.stringify(old));
            this.getSearchRecord();
        },
        /**
         * @method 获取搜索记录
         */
        getSearchRecord() {
            let r = JSON.parse(localStorage.getItem(`${localStorageKey.search_exam_key}${this.searchType}`));
            if (r && Array.isArray(r)) {
                r.reverse();
            }
            else { r = []; }
            this.searchRecord = r;
        },
        /**
         * @method 获取考场列表
         * 
         */
        showExamRoomList(txt, pageIndex = 1, pageSize = 5) {
            let param = { roomName: txt, cityId: null, pageIndex: pageIndex, pageSize: pageSize }
            this.getExamSearchRoomList(param);
        },

        /**
         * 切换tab
         */
        changeTab(event) {
            let target = event.target;
            let stype = target.getAttribute("data-type");
            if (stype === '1' || stype === '2') {
                this.searchType = parseInt(stype);
                this.getSearchRecord();
            }

        }
    },
    computed: {
        ...mapGetters([
            "examSearchRoomList",
        ])
    },
    created() {
        // this.clearExamRoomList();
        this.getSearchRecord();
    },
    components: {
        search,
        'common-header': Header,
        examitem
    }
}
</script>

<style lang="less" scoped>
@import "../assets/css/demo.less";

.icon(@w,@h,@x,@y) {
  display: inline-block;
  background: url("../assets/images/icon.png");
  width: @w;
  height: @h;
  margin-right: 16/@r;
  background-position: @x @y;
  background-size: 640/@r 200/@r;
  vertical-align: middle;
  content: "";
}
.search_top {
  margin-top: 2.2rem;
  background: #fff;
  padding: 21/@r 0 21/@r 30/@r;
  box-sizing: border-box;
  .search_cancel {
    float: right;
    margin-right: 32/@r;
    font-size: 24/@r;
    color: #707c7f;
    line-height: 1.5rem;
  }
}
.tab_warp {
  border-bottom: solid 1px #e9eef0;
  background: #fff;
  height: 72/@r;
  line-height: 72/@r;
  box-sizing: border-box;
  font-size: 26/@r;
  padding: 0 80/@r;
  .tab_item {
    display: inline-block;
    height: 100%;
    width: 160/@r;
    text-align: center;
    color: #354d5a;
  }
  .tab_item.active {
    color: #5070ad;
    border-bottom: solid 1px #5070ad;
  }

  .left {
    float: left;
  }
  .right {
    float: right;
  }
}

.search_recent {
  background: #fff;
  padding: 50/@r 30/@r;
  .search_recent_tite {
    color: #909da0;
    font-size: 22/@r;
  }
  .search_recent_list {
    display: block;
    margin-top: 24/@r;
    .search_recent_item {
      font-size: 24/@r;
      color: #354d5a;
      border-bottom: solid 1px #e9eef0;
      padding: 26/@r 0;
      &:before {
        .icon(24/@r,24/@r,-50/@r,-9/@r);
        margin-top: -0.05rem;
      }

      .icon_del {
        cursor: pointer;
        .icon(17/@r,17/@r,-151/@r,-50/@r);
        float: right;
        margin: 8/@r 0 0 0;
      }
    }
  }
  .no_data_warp {
    display: none;
  }
}
.search_recent.no_data {
  background: none;
  .search_recent_list {
    display: none;
  }
  .no_data_warp {
    display: block;
    .no_data_img {
      background: url("../assets/images/icon.png") -200/@r -10/@r;
      background-size: 640/@r 200/@r;
      width: 120/@r;
      height: 85/@r;
      margin: 0 0 20/@r 8/@r;
    }
    width: 150/@r;
    font-size: 22/@r;
    color: #909da0;
    margin: 160/@r auto;
  }
}
</style>




